@import url('https://fonts.googleapis.com/css2?family=IBM+Plex+Sans:wght@200&display=swap');
* {
    box-sizing: border-box;
  }
  
  body {
    font-family: 'Kodchasan', sans-serif;
  }
  
  /* Float four columns side by side */
  .column {
    float: left;
    width: 25%;
    padding: 0 20px;
    
  }
  
  /* Remove extra left and right margins, due to padding in columns */
  .row {
      margin-top: 50px;
      margin: 4 -5px;
    }
  
  /* Clear floats after the columns */
  .row:after {
    content: "";
    display: table;
    clear: both;
  }
  
  /* Style the counter cards */
  .card {
    box-shadow: 0 6px 8px 0 rgba(219, 85, 8, 0.404); /* this adds the "card" effect */
    padding:20px;
    text-align: center;
    background-color: #252525;
  }
  .card > h1{
    font-family: 'IBM Plex Sans', sans-serif;
    letter-spacing: var(--chakra-letterSpacings-widest);
    font-size: larger;
    font-weight: bolder;
    color:white;
  }
  .card > p{
    font-size: larger;
    font-weight: bolder;
    color:white;
  }
  
  /* Responsive columns - one column layout (vertical) on small screens */
  @media screen and (max-width: 600px) {
    .column {
      width: 100%;
      display: block;
      margin-bottom: 20px;
    }
  }